.main-layout {
  padding: 0px;
  margin: 0px;
  min-height: 100vh !important;
  background: #fafafa;
}
.main-content {
  margin: 10px 0px;
  padding: 0px;
}
.logo {
  padding: 20px 40px 40px 20px;
  color: white;
  font-size: 24px;
  line-height: 24px;
  background: var(--palette-gray-darken-3);
}
.ant-layout-sider-collapsed .logo-title {
  display: none !important;
}
.logo-title {
  padding-left: 60px;
  font-size: 18px;
  line-height: 18px;
}
.logo img {
  height: auto;
  width: 100%;
}

.ant-menu-light li:hover {
  background: #f9e090 !important;
}

.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
  border-right: 1px solid transparent !important;
}

.menu-sider li.ant-menu-item i {
  font-size: 18px !important;
  vertical-align: middle;
  height: 40px;
  width: 40px;
  padding: 8px;
  font-size: 24px !important;
  border-radius: 50%;
  text-align: center;
}
.ant-layout-sider-collapsed .menu-sider li.ant-menu-item i {
  transform: translate(-10px, 0px) !important;
  padding: 0px !important;
}

.ant-layout-sider {
  width: 250px !important;
  flex: 0 0 250px !important;
  max-width: 250px !important;
  min-width: 250px !important;
}
.ant-layout-sider .ant-layout-sider-trigger {
  width: 250px !important;
}
.ant-layout-sider-collapsed .ant-layout-sider-trigger {
  width: 80px !important;
}
.ant-layout-sider-collapsed {
  width: 80px !important;
  flex: 0 0 80px !important;
  max-width: 80px !important;
  min-width: 80px !important;
}

/* Theme Light */
.ant-layout-sider-light .menu-sider li.ant-menu-item i {
  background: white;
  color: #263342;
}
.ant-layout-sider-light .menu-sider li.ant-menu-item-selected i {
  background: #2f3c4c !important;
  color: white !important;
}
.ant-layout-sider-light .menu-sider li.ant-menu-item.ant-menu-item-selected{
  background: var(--palette-yellow);
}
.ant-layout-sider-light .menu-sider li.ant-menu-item {
  background: var(--palette-gray-darken-1);
  margin-bottom: 3px !important;
  
}
