.header-home {
  display: block;
  /*  max-height: 40px; */
  /*  margin-bottom: 20px; */
}
.header-home .ant-layout-header {
  height: 100px !important;
  padding-right: 0px;
  padding-left: 0px;
}
.header-home .ant-layout-header .ant-breadcrumb {
  color: var(--palette-white);
  font-size: 18px;
  font-weight: bold;
}
.item-menu-recent-projects {
  font-size: 12px !important;
  cursor: default !important;
}

.ant-dropdown-menu-light li:first-child,
.ant-dropdown-menu-light li:nth-child(2) {
  color: var(--palette-purple);
}
.ant-dropdown-menu-light li:nth-child(+ n + 5) {
  background-color: red !important;
}
.ant-dropdown-menu-light li:hover {
  background-color: #ffffff !important;
}
/* header.ant-layout-header[theme=dark] */
.header-home .ant-dropdown-menu-item-active {
  background: none !important;
}
.ant-avatar-image {
  /* background: #fff !important; */
}
.item-menu-circle {
  /* border-color: var(--palette-purple); */
  border: 2px solid var(--palette-white) !important;
  height: 40px !important;
  width: 40px !important;
  padding: 5px !important;
  margin: 8px;

  background-position: center;
  background-size: 100%;
  padding: 8px !important;
}
.item-avatar {
  border: none !important;
  height: 50px !important;
  width: 50px !important;
}
.nav-bar {
  background: transparent !important;
  border-bottom: 2px solid transparent !important;
}

.nav-bar .ant-menu,
.nav-bar .ant-menu-item:hover,
.nav-bar .ant-menu-item.ant-menu-item-selected {
  background: none !important;
  color: #ffffff !important;
  border-bottom: 2px solid transparent !important;
}
.nav-bar .ant-menu-item:focus {
}

/* Menu Light */
.header-home .menu-home.ant-menu-light,
.header-home .menu-home.ant-menu-light li,
.header-home .menu-home.ant-menu-light li:hover {
  background: transparent !important;
  border: 0px !important;
  color: #fff !important;
}
.header-home .menu-home.ant-menu-light .ant-dropdown-link {
  color: var(--palette-yellow);
}
.header-home .menu-home.ant-menu-light .ant-breadcrumb-separator {
  color: #fff;
}
/* .header-home .menu-home.ant-menu-light .bread-crumb-header a {
} */
@media (max-width: 375px) {
  .side-bar {
    display: none;
  }
}
