body,
html {
  font-family: "Roboto" !important;
}

/* Variables */
:root {
  --icon-size-large: 28px;

  --font-size-large: 28px;
  --font-size-medium: 20px;
  --font-size-small: 18px;
  --font-size-tiny: 16px;

  --palette-blak: #000;
  --palette-gray: rgba(160, 160, 160);
  --palette-white: #fff;

  /* Light Variables */
  --palette-yellow: #f9e090;
  --palette-beige: #ff935c;
  --palette-orange: #dc5353;
  --palette-purple: #a72461;
  /* Dark Variables */
  --palette-gray-darken-1: #516680;
  --palette-gray-darken-2: #3d4d61;
  --palette-gray-darken-3: #2f3c4c;
  --palette-gray-darken-4: #263342;
}
/* Texts */
.text-yellow {
  color: var(--palette-yellow) !important;
}
.text-beige {
  color: var(--palette-beige) !important;
}
.text-orange {
  color: var(--palette-orange) !important;
}
.text-purple {
  color: var(--palette-purple) !important;
}
.text-white {
  color: var(--palette-white) !important;
}
.text-gray {
  color: var(--palette-gray) !important;
}
.text-blak {
  color: var(--palette-blak) !important;
}
.text-gray-darken-1 {
  color: var(--palette-gray-darken-1) !important;
}
.text-gray-darken-2 {
  color: var(--palette-gray-darken-2) !important;
}
.text-gray-darken-3 {
  color: var(--palette-gray-darken-3) !important;
}
.text-gray-darken-4 {
  color: var(--palette-gray-darken-4) !important;
}
/* Backgrounds */
.bg-yellow {
  background-color: var(--palette-yellow) !important;
}
.bg-beige {
  background-color: var(--palette-beige) !important;
}
.bg-orange {
  background-color: var(--palette-orange) !important;
}
.bg-purple {
  background-color: var(--palette-purple) !important;
}
.bg-white {
  background-color: var(--palette-white) !important;
}
.bg-gray {
  background-color: var(--palette-gray) !important;
}
.bg-blak {
  background-color: var(--palette-blak) !important;
}
.bg-gray-darken-1 {
  background-color: var(--palette-gray-darken-1) !important;
}
.bg-gray-darken-2 {
  background-color: var(--palette-gray-darken-2) !important;
}
.bg-gray-darken-3 {
  background-color: var(--palette-gray-darken-3) !important;
}
.bg-gray-darken-4 {
  background-color: var(--palette-gray-darken-4) !important;
}
.icon-orange {
  background-color: var(--palette-orange) !important;
}
.icon-yellow {
  background-color: var(--palette-yellow) !important;
}

.App {
  text-align: center;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
